.statistics {
    width: 512px;
    margin: auto;
    background: #ffffff;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 0px 0px 4px 4px;
    }

.title {
    text-align: center;
    padding: 30px;
}

.statList{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    list-style: none;
    background-color: #dad7d7;
    padding: 25px;
    
}

.item{
    display: block;}

.label{    
    display: block;
    text-align: center;}

